import React from 'react';
import { Redirect } from 'react-router-dom';
import { withAuthRedirect } from '../../hoc/withAuthRedirect'

const News = (props) => {
    if (!props.isAuth) return <Redirect to='/login' />
    return (
        <div>
            Нерабочая страница
        </div>
    );
}
export default withAuthRedirect(News);